import React, {Component} from 'react';

class bottomContainer extends Component {
    constructor(props){
        super(props);
        this.state={
            options: ['GENERAL', 'ELENCO', 'PRINCIPALES PREMIOS'],
            actions:[
                { imgsrc:'./images/add-gray-s.svg', text:'Mi lista', alt:'Add to list'},
                { imgsrc:'./images/sad-gray-w.svg', text:'Evaluar', alt:'Evaluate'},
                { imgsrc:'./images/rec-gray-s.svg', text:'Grabar', alt:'Record'},
                { imgsrc:'./images/share-gray-s.svg', text:'Compartir', alt:'Share'},
            ],
            selectedOption: 'GENERAL',
        }
    }
    selectOption(option){
        this.setState({
            selectedOption: option
        })
    }
    renderInfo(option){
        if (option === this.state.options[0]){
            return (
                <div className="general-info">
                    <div className="actions">
                        {this.state.actions.map( (action) => {
                            return(
                                <div key={`generalinfo_${action.alt}`} className="action">
                                    <img className="action-img" src={action.imgsrc} alt={action.alt} />
                                    <div className="action-text">{action.text}</div>
                                </div>
                            )
                        } )}
                    </div>
                    <div className="synopsis">
                        <div className="synopsis-title">SINOPSE</div>
                        <div className="synopsis-text">{this.props.showData.Synopsis}</div>
                    </div>
                </div>
            )
        } else if (option === this.state.options[1]){
            return(
                <div className="cast-info">
                    <div className="cast">{this.props.showData.Cast.map(actor => actor.Name).join(', ')}</div>
                </div>
            )
        } else if (option === this.state.options[2]){
            return(
                <div className="awards-info">
                    <div className="awards">Sem informa√ß√£o dispon√≠vel.</div>
                </div>
            )
        }
    }
    render(){
        return(
            <div className="bottom-container">
                <div className="bottom-options">
                    {this.state.options.map( (option) => { 
                        const focusclass = this.state.selectedOption === option? 'option-focus':'';
                        return(
                            <button key={`${option}_button`} className={`option ${focusclass}`} onClick={(e)=>{this.selectOption(option)}}>{option}</button>
                        )
                        })}
                    <img src="./images/logo-telecine.svg" className="logo"></img>
                </div>
                {this.renderInfo(this.state.selectedOption)}     
            </div>
        );
    }
}

export default bottomContainer;