import React, {Component} from 'react';
import EpisodeImg from './EpisodeImg';
import EpisodeInfo from './EpisodeInfo';
import EpisodeTitle from './EpisodeTitle';

class episode extends Component {
    constructor(props){
        super(props);
        this.state = { collapsed: true}
    }
    toggle() {
        this.setState({ collapsed: !this.state.collapsed });
    };
    render(){
        return(
            <div className='episode'>
                <EpisodeTitle title={this.props.title} episodeNumber={this.props.episodeNumber} handler={(e) => this.toggle()}/>
                {!this.state.collapsed && <div>
                    <EpisodeImg image={this.props.image}/>
                    <EpisodeInfo synopsis={this.props.synopsis}/>
                </div>}
            </div>
        )
    }
}

export default episode;