import React, {Component} from 'react';
import EpisodeList from './EpisodeList';

class sidebar extends Component{
    constructor(props){
        super(props);
        this.state = {
            selectedSeason: this.props.seasonNumber[0]
        };
    }
    toggleSeason(season){
        this.setState({
            selectedSeason: season
        });
    }

    render(){
        return(
            <div className="side-bar">
                <div className="close-search">
                    <img src="./images/close-search-w.svg" alt="Close" />
                </div>
                <div className="seasons">
                    {this.props.seasonNumber.map( (season) => {
                        const focusclass = this.state.selectedSeason === season? 'season-focus':'';
                        return(
                            <button key={`${season}_season`} className={`season ${focusclass}`} onClick={(e)=>{this.toggleSeason(season)}}>T{season}</button>                        
                        )
                    } )}
                </div>
                {this.props.seasonNumber.map( (season) => {
                    return(
                        season === this.state.selectedSeason ? 
                            <EpisodeList key={`${season}_ep`} episodes={this.props.episodeData.filter(episode => episode.SeasonNumber === season)}/> : ''                    
                    )
                } )}
                
            </div>
        )}
}

export default sidebar;