import Sidebar from './components/Sidebar';
import BottomContainer from './components/BottomContainer';
import React, { Component } from 'react';
import axios from 'axios';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: {},
      episodes: {},
      seasonNumber: {},
      errorMsg: '',
      loaded: false,
    };  
    }
  getData = async (url) => {
    try{
      const response = await axios(url);
      if (!response.data){
        this.setState({errorMsg: ('Erro na busca de dados üò≠')});
      }
      return response.data;
    } catch(err){
      this.setState({errorMsg: ('Erro na busca de dados üò≠')});
    }
  }
  getSeasons = episodeData => {    
    return [...new Set(episodeData.map(episode => episode.SeasonNumber))];
  }
  async componentDidMount(){
    const showDataUrl = 'https://sample-api-78c77.firebaseio.com/tv-shows/SHOW123.json';
    const episodeDataUrl = 'https://sample-api-78c77.firebaseio.com/episodes/SHOW123.json';
    const showData = await this.getData(showDataUrl);
    const episodeData = await this.getData(episodeDataUrl);
    if(showData && episodeData){
      var episodeFiltered = episodeData.filter(function (el) {
        return el != null;
      });
      const seasons = this.getSeasons(episodeFiltered);
      this.setState({
        show: showData,
        episodes: episodeFiltered,
        seasonNumber: seasons,
        loaded: true
      });
    }
  }
  render() {
    return (
      (this.state.errorMsg !== '') ?
      (<div className="error-pg"><h1>{this.state.errorMsg}</h1></div>) : this.state.loaded ?
        (<div className="container" style={{background: `url(${this.state.show.Images.Background}) no-repeat center center fixed`}}>
          <div className="series-title">
            <h1>{this.state.show.Title}</h1>
            <h2>80% INDICADO / CIENCIA FICCI√ìN / {this.state.show.Year} / EUA / 14</h2>
          </div>
          {this.state.episodes.length > 0 && <Sidebar episodeData={this.state.episodes} seasonNumber={this.state.seasonNumber}/>}
          <BottomContainer showData={this.state.show}/>
        </div>) : ''
    );
  }
}

export default App;
