import React from 'react';

const episodeTitle = (props) => {
    return(
        <div className="episode-headline" onClick={(e) => props.handler()}>
            <div className="episode-title">
                {props.episodeNumber} {props.title}
            </div>
            <div className="episode-play">
                <img src="./images/play-small-player-w.svg" alt="Play" />
            </div>
        </div>
    )
}

export default episodeTitle;